/*
* 经营看板-人员情况表store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from "@/utils/api";
import Schemas from "@/utils/Schemas/wisdomsee";


class HumanCostStore {
   @observable overviewTableData = []; // 整体情况表体数据
   @observable costArchTableData = []; // 人力成本结构表体数据
   @observable deptCostTableData = []; // 各部门人力成本表体数据

   @observable costArchLegendData = []; // 人力成本结构图表legend数据
   @observable costArchChartData = []; // 人力成本结构图表数据

   @observable deptCostXData = []; // 各部门人力成本图表x轴数据
   @observable deptCostChartData = []; // 各部门人力成本图表数据
   
   @observable overviewTableColumn = []; //人才结构表头数据
   @observable costArchTableColumn = []; //人力成本结构表体数据
   @observable deptCostTableColumn = []; //人力成本结构表体数据


   @observable lastUpdatedTime = "";



	// 根据分类查询报表
	@action
	getReportByClassifyId(param) {
		return requestPost('getReportByClassifyId', Config.wisdomsee.getReportByClassifyId, Schemas.wisdomsee.getReportByClassifyId, param)
	}

	// 看板分类查询
	@action
	getReportClassifys (param) {
		return  requestPost('getReportClassifys', Config.wisdomsee.getReportClassifys, Schemas.wisdomsee.getReportClassifys,
			param)
	}


}


export default HumanCostStore;



