import React from 'react';
import url from 'url';
import {inject, observer} from 'mobx-react';
import {Breadcrumb, Col, Row, Select, Table, Card, Menu, Dropdown, Button, Icon, ConfigProvider} from 'antd';
import {Scrollbars} from 'react-custom-scrollbars';
import ReactEcharts from "echarts-for-react";
import moment from "moment";
import HumanCostStore from '@/store/kanban/HumanCostStore';
import * as mobx from 'mobx';

const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center',padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png' />
		<p style={{
			fontSize:'14px',
			fontFamily:'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);
const {Option} = Select;

@inject('kanbanStore')
@observer
export default class HumanConst extends React.Component {
	constructor(props) {
		super(props);
		this.store = new HumanCostStore();
		this.state = {
			id: '13', // 表id
			value: '年度',
			key: '1',
			value1: '2019',
			yearData: [{
				id: '',
				name: '2019'
			}],  // 年份
			selData: [
				{
					id: '01',
					name: '1月'
				},
				{
					id: '02',
					name: '2月'
				},
				{
					id: '03',
					name: '3月'
				},{
					id: '04',
					name: '4月'
				}
				,{
					id: '05',
					name: '5月'
				}
				,{
					id: '06',
					name: '6月'
				}
				,{
					id: '07',
					name: '7月'
				},{
					id: '08',
					name: '8月'
				},{
					id: '09',
					name: '9月'
				},{
					id: '10',
					name: '10月'
				},{
					id: '11',
					name: '11月'
				},{
					id: '12',
					name: '12月'
				},{
					id: 'season1',
					name: '1季度'
				},{
					id: 'season2',
					name: '2季度'
				},{
					id: 'season3',
					name: '3季度'
				},{
					id: 'season4',
					name: '4季度'
				},{
					id: 'fullyear',
					name: '年度'
				}
			],
			time:null,
			month: '', //月份
		}
	}

	componentDidMount() {
		// 获取当前年份数据
		const currentYear = moment().year();
		/*
		* 年份数据来源：从2019年开始， 截至到当前的年份
		* */
		if (currentYear<=2019) {
			this.setState({
				yearData: [{
					name: '2019'
				}]
			});
		} else {
			const num = Number(currentYear);
			let data = [];
			for (let i=2019;i<=num;i++) {
				data.push({name:String(i)})
			}
			this.setState({
				yearData: Object.assign([], data.reverse())
			});
		}
		const data = {
			'01': '1月',
			'02': '2月',
			'03': '3月',
			'04': '4月',
			'05': '5月',
			'06': '6月',
			'07': '7月',
			'08': '8月',
			'09': '9月',
			'10': '10月',
			'11': '11月',
			'12': '12月',
			'season1': '1季度',
			'season2': '2季度',
			'season3': '3季度',
			'season4': '4季度',
			'fullyear': '年度'
		}
		const mont = moment().month() + 1;
		let search = this.props.location.search;
		const id = url.parse(search, true).query.id;
		const key = url.parse(search, true).query.key;
		const time = url.parse(search, true).query.time;
		const mon = url.parse(search, true).query.month;
		let month = mon == undefined ? mont : mon;
		let lastTime = null; // 最后更新时间

		// 没有时间
		if(time==undefined) {
			// 根据id查询时间
			this.getLastUpdatedTime().then((res) => {
				if (res) {
					let capitalInfo = res.filter(item => {
						return item.code === "rlcb"
					});
					if (capitalInfo.length > 0) {
						lastTime = capitalInfo[0]['lastUpdateTime'];
						this.setState({
							time: Number(lastTime)
						})
					}
				}
			});
		} else {
			this.setState({
				time: Number(time)
			})
		}

		this.setState({
			id,
			key,
			value: data[month],
			month: month.toString(),
			value1:currentYear.toString()
		}, () => {
			// 获取初始数据
			this.handleChange();
		});
	}

	componentWillUnmount() {

	}

	// 获取最后时间
	getLastUpdatedTime = () => {
		const params = {
			"level": 2,
			"parentId": ""
		}
		return this.store.getReportClassifys(params);
	}

	handleChange = ( ) => {
		const {month, value1, id} = this.state;
		let params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": month};
		if (month === "season1") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "03"};
		} else if (month === "season2") {
			params = {"classifyId": id, "year": value1, "beginMonth": "04", "endMonth": "06"};
		} else if (month === "season3") {
			params = {"classifyId": id, "year": value1, "beginMonth": "07", "endMonth": "09"};
		} else if (month === "season4") {
			params = {"classifyId": id, "year": value1, "beginMonth": "10", "endMonth": "12"};
		} else if (month === "fullyear") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "12"};
		}
		this.getReportByTime(params);
	 }

	onChange = () => {

	}

	onSelect = d => {
		console.log('select')
		console.log(d);
	}

	disabledDate = (current) => {
		console.log(current, '当前')
		//return current && current.valueOf() > Date.now();
	}

	getReportByTime =(params) =>{
		this.renderData(params);
	}

	renderData =(params)=> {
		this.store.getReportByClassifyId(params).then((res)=>{
			if(res){
				res.map((item, index)=>{
					this.fillTableColumn(item) //填充表头数据
				})
			}
			return res
		}).then((res)=>{
			if(res){
				res.map((item, index)=>{
					this.fillTableData(item) // 填充表体数据
					this.fillChartData(item) // 填充图表数据
				})
			}
		});
	}

	fillTableColumn = (item) => {
		if(item.name==="人力成本整体情况") {
			if(item.schemas){
				this.store.overviewTableColumn = item.schemas.map(item=>{
					return  Object.assign({}, {'dataIndex':item.name}, {'title':item.title}, {'align':'center'})
				});

			}
		}
		if(item.name==="人力成本结构") {
			if(item.schemas){
				this.store.costArchTableColumn = item.schemas.map((vitem, index)=>{
					if (index==0) {
						return  Object.assign({}, {'dataIndex':vitem.name}, {width:'180px', fixed: 'left', 'title':vitem.title}, {'align':'center'})
					} else {
						return  Object.assign({}, {'dataIndex':vitem.name},
							{'title':vitem.title}, {'align':'center'})
					}
				});
			}
		}
		if(item.name==="各部门人力成本") {
			if(item.schemas){
				this.store.deptCostTableColumn = item.schemas.map((item, index)=>{
					if (index==0) {
						return  Object.assign({}, {'dataIndex':item.name}, {width:'180px',fixed: 'left', 'title':item.title}, {'align':'center'})
					} else {
						return  Object.assign({}, {'dataIndex':item.name}, {'title':item.title}, {'align':'center'})
					}
				});
			}
		}
	 }

	 fillTableData = (item) => {
		if(item.name==="人力成本整体情况") {
			if(item.datas){
				this.store.overviewTableData = item.datas;
			}

		}
		if(item.name==="人力成本结构") {
			if(item.datas){
				this.store.costArchTableData = item.datas;
			}
		}
		if(item.name==="各部门人力成本") {
			if(item.datas){
				this.store.deptCostTableData = item.datas;
			}
		}
	 }

	 fillChartData(item) {
		if(item.name==="人力成本结构") {
			if(item.charts){
				this.store.costArchLegendData = item.charts.category;
				this.store.costArchChartData = item.charts.series
			}
		}
		if(item.name==="各部门人力成本") {
			if(item.charts){
				this.store.deptCostXData = item.charts.category;
				this.store.deptCostChartData = item.charts.series
			}
		}
	 }

	handleMenuClick = (e) => {
		let value = e.key;
		this.setState({
			value: e.item.props.children,
			month: value
		}, ()=>{
			this.handleChange();
		});
	}

	// 切换年份
	handleYearClick = (e) => {
		this.setState({
			value1: e.item.props.children
		},()=>{
			this.handleChange();
		})
	}

	totalFontBold=(text, row, index)=>{
		if (text==="合计") {
			return {
				children: <span >{text}</span>,
				props: {
					className:'tab_td_bg fwb'
				}
			};
		  }
		  return text
	}


	render() {
		const {yearData, selData, value, value1, key, time} = this.state;
		const menu = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleMenuClick}>
					{selData.map((item, index) => {
						return (
							<Menu.Item key={item.id}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
	    );
		const menu1 = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleYearClick}>
					{yearData.map((item, index) => {
						return (
							<Menu.Item key={'year' + index}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		var option1 = {
			title: {
				text: '人力成本结构图',
				left:'center',
				textStyle:{
					color: 'rgb(61, 77, 102)',
					fontWeight:'bold',
					fontStyle:'normal',
					fontFamily: 'Microsoft YaHei UI',
					fontSize:'13'
				}
			},
			tooltip: {
				trigger: 'item',
				formatter: "{b} : {c} <br/>{a} : {d}%"
			},
			legend: {
				bottom: 0,
				data: this.store.costArchLegendData,
			},
			toolbox: {
				show: true,
				feature: {
					mark: {show: true},
					dataView: {show: false, readOnly: false},
					magicType: {
						show: true,
						type: ['pie', 'funnel'],
						option: {
							funnel: {
								x: '25%',
								width: '50%',
								funnelAlign: 'left',
								max: 1548
							}
						}
					},
					restore: {show: false},
					saveAsImage: {show: false}
				}
			},
			calculable: true,
			series: [
				{
					name: '占比',
					type: 'pie',
					center: ['50%', '50%'],
					radius: ['40%', '65%'],
					label: {
						normal: {
							show: false,
							formatter: "{d}%",
							position: 'inner',
							textStyle: {
								color: '#FFF',
								fontSize: 12
							}
						}
					},
					data: this.store.costArchChartData,
					color: ['#87BDFA', '#63C8C6', '#BADBF6', '#AEDFF1', '#8EABD0', '#47BAC1','#9C96F1','#A6E59F' ]
				}
			]
		};

		let option2 = {
			color: ['#B5E3E5', '#47BAC1', '#B5E3E5'],
			title: {
				text: '各部门人力成本结构图',
				left:'center',
				textStyle:{
					color: 'rgb(61, 77, 102)',
					fontWeight:'bold',
					fontStyle:'normal',
					fontFamily: 'Microsoft YaHei UI',
					fontSize:'13'
				}
			},
			tooltip: {
				trigger: 'axis',
				axisPointer: {
					type: 'shadow'
				}
			},
			grid: {
				left: '2%',
				right: '4%',
				containLabel: true
			},
			toolbox: {
				show: true,
				feature: {
					mark: {show: true},
					dataView: {show: false, readOnly: false},
					magicType: {show: false, type: ['line', 'bar']},
					restore: {show: false},
					saveAsImage: {show: false}
				}
			},
			calculable: true,
			xAxis: [
				{
					type: 'category',
					data: this.store.deptCostXData,
				}
			],
			yAxis: [
				{
					type: 'value',
					scale : true,
					axisLine:{
						show:false
					},
					axisTick:{
						show:false
					},
				}
			],
			series : [
				{
					name:'成本',
					type:'bar',
					barWidth: '45%',
					itemStyle: {
						emphasis: {
							barBorderRadius: 3
						},
						normal: {
							barBorderRadius: 3
						}
					},
					data:this.store.deptCostChartData
				}
			]
		};
		const {defaultKey} = this.props.kanbanStore;


		return (
			<div>
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/home?key="+defaultKey}>经营看板</Breadcrumb.Item>
						<Breadcrumb.Item href={"#/home?key=" + key}>财务</Breadcrumb.Item>
						<Breadcrumb.Item>人力成本表</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Row>
					<Col span={24}>
						<div className="mt20 mb20">
							<span className="con_title">人力成本表 </span>
							<span className="con_time ml35">最近更新时间:</span><span className="con_time_content ml15">{moment(time).format("YYYY-MM-DD HH:mm:ss")}</span>
						</div>
					</Col>
					<Col span={24}>
						<Dropdown overlay={menu1}>
							<Button style={{width: 82}}>
								{value1} <Icon type="down"/>
							</Button>
						</Dropdown>
						<Dropdown overlay={menu}>
							<Button className="ml15" style={{width: 82}}>
								{value} <Icon type="down"/>
							</Button>
						</Dropdown>
					</Col>
				</Row>
				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">人力成本整体情况（单位：万元）</h3>
					<Table columns={this.store.overviewTableColumn}
					       dataSource={this.store.overviewTableData}
					       size={"small"}
					       pagination={false}
						   title={() => '整体情况表'}
						   className="ci_table2"
						   tableLayout={"fixed"}
					       bordered/>
				</Card>
				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">人力成本结构情况（单位：万元）</h3>
					<Row>
						<Col span={10}>
							<ReactEcharts
								notMerge={true}
								lazyUpdate={true}
								ref={(node) => { this.charts = node }}
								style={{width:'100%',height:'448px'}}
								option={option1}/>
						</Col>
						<Col span={14}>
							<Table columns={this.store.costArchTableColumn}
							       dataSource={this.store.costArchTableData}
							       size={"small"}
							       pagination={false}
								   title={() => '人力成本结构明细'}
								   className="ci_table2 ci_table5"
								   rowClassName={(record, index) => {
									   let className = index % 2 ? 'shallow_gray' : 'shallow_blue';
									   return className
								   }}
								   scroll={{ x: mobx.toJS(this.store.costArchTableColumn).length*100 }}
							       bordered/>
						</Col>
					</Row>
				</Card>
				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">各部门人力成本结构情况（单位：万元）</h3>
					<Row>
						<Col span={10}>
							<ReactEcharts
								notMerge={true}
								lazyUpdate={true}
								ref={(node) => { this.charts2 = node }}
								style={{height:'346px'}}
								option={option2}/>
						</Col>
						<Col span={14}>
							<Table columns={this.store.deptCostTableColumn}
							       dataSource={this.store.deptCostTableData}
							       size={"small"}
							       pagination={false}
								   title={() => '各部门人力成本结构明细'}
								   className="ci_table2 ci_table5"
								   rowClassName={(record, index) => {
									   let className = index % 2 ? 'shallow_gray' : 'shallow_blue';
									   return className
								   }}
								   scroll={{ x: mobx.toJS(this.store.deptCostTableColumn).length*100 }}
							       bordered/>
						</Col>
					</Row>
				</Card>
				</ConfigProvider>
			</div>
		);
	}
}





